import React, { useState, useEffect } from "react";
import Hamburger from "./Ui/Hamburger";
import cloud_top from ".././../../static/Images/header/cloud-top.svg";
import logo from ".././../../static/logo/logo.avif";
import cloud_bottom from ".././../../static/Images/header/cloud-bottom.svg";
import rating_stars from ".././../../static/Images/header/star.svg";
import { useGlobal } from "../context/GlobalContext";
import {Link} from "gatsby" 
const Navbar = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
const {state} = useGlobal()
  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  // Prevent body scrolling when menu is open
  useEffect(() => {
    if (isMenuOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'auto';
    }
    
    return () => {
      document.body.style.overflow = 'auto';
    };
  }, [isMenuOpen]);

  // Navigation links data
  const navLinks = [
    { path: "/", label: "Home" },
    { path: "/services", label: "Services" },
    { path: "/about", label: "About" },
    { path: "/contact", label: "Contact" }
  ];

  // Render navigation links
  const renderNavLinks = (isMobile = false) => (
    <>
      {navLinks.map((link) => (
        <a 
          key={link.path} 
          href={link.path}
          {...(isMobile && { onClick: toggleMenu })}
        >
          {link.label}
        </a>
      ))}

    </>
  );

  return (
    <header className=" navbar-header-main-container">
    <div className="navbar-container container">
        {/* Background Clouds */}
      <div className="clouds" aria-hidden="true">
        <picture>
          <source
            media="(max-width: 600px)"
            srcSet={cloud_top}
          />
          <img
            src={cloud_top}
            alt=""
            className="cloud-top"
          />
        </picture>
        <picture>
          <source
            media="(max-width: 600px)"
            srcSet={cloud_bottom}
          />
          <img
            src={cloud_bottom}
            alt=""
            className="cloud-bottom"
          />
        </picture>
      </div>
   
      {/* Actual Navbar Content */}
      <div className="navbar-content">
        <div className="nav-links-container container">
          <figure className="header-logo">
           <a href="/" className="header-logo-link" aria-label="Company Logo">
             <img src={logo} alt="Company Logo" className="main-logo" />
           </a>
            <figcaption className="header-logo-caption">
              <div className="header-ratings-container">
                <p className="header-ratings">4,8</p>
                <div className="header-ratings-stars">
                  {[...Array(5)].map((_, i) => (
                    <img
                      key={i}
                      height={30}
                      width={30}
                      src={rating_stars}
                      alt="website ratings"
                    />
                  ))}
                </div>
              </div>
              <span className="header-total-counts">2,290 ratings</span>
            </figcaption>
          </figure>

          <Hamburger isOpen={isMenuOpen} toggleMenu={toggleMenu} />

          {/* Desktop Navigation */}
        
            <nav className="nav-links" role="navigation" aria-label="Main navigation">
            {renderNavLinks()}
           <button  class="cart-btn" >
  <Link to="/cart" class="cart-btn" >
    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke-width="1.5" stroke="currentColor" class="size-6">
  <path stroke-linecap="round" stroke-linejoin="round" d="M15.75 10.5V6a3.75 3.75 0 1 0-7.5 0v4.5m11.356-1.993 1.263 12c.07.665-.45 1.243-1.119 1.243H4.25a1.125 1.125 0 0 1-1.12-1.243l1.264-12A1.125 1.125 0 0 1 5.513 7.5h12.974c.576 0 1.059.435 1.119 1.007ZM8.625 10.5a.375.375 0 1 1-.75 0 .375.375 0 0 1 .75 0Zm7.5 0a.375.375 0 1 1-.75 0 .375.375 0 0 1 .75 0Z" />
</svg>

  <span class="msg-count">{state?.cart?.length <= 0 ? 0 : state?.cart?.length }</span>
  </Link>
</button>

          </nav>
         
        
          
          {/* Mobile Navigation */}
          <div className={`mobile-menu ${isMenuOpen ? 'open' : ''}`}>
            <nav className="mobile-nav-links" role="navigation" aria-label="Mobile navigation">
              {renderNavLinks(true)}
            </nav>
          </div>
        </div>
      </div>
    </div>
    </header>
  );
};

export default Navbar;